.block--container.nav {
  padding: 2rem;
}
.block--links a {
  padding-right: 1rem;
  text-decoration: unset;
  text-transform: uppercase;
  color: #fff;
  &:last-child {
    padding-right: 0;
  }
}
.block-logo img {
  max-height: 80px;
}

@media (max-width: 576px) {
  .block--container.nav {
    padding: 2rem 1rem;
    .block-logo {
      margin-bottom: 1rem;
    }
  }
}
